import { useEffect } from "react";
import anime from "animejs";

interface Props {
    data: any;
}

const Carousel: React.FC<Props> = ({ data }) => {
    console.log("carousel data :>> ", data);
    useEffect(() => {
        if (!!data && data.length !== 0) {
            var elems = document.querySelectorAll(".carousel");
            M.Carousel.init(elems, {
                fullWidth: true,
                indicators: true,
            });
        }
    });

    if (!!data && data.length !== 0) {
        setTimeout(() => {
            try {
                var elem = document.getElementById("carousel");
                console.log("elem :>> ", elem);
                if (!!elem) {
                    let instance = M.Carousel.getInstance(elem);
                    instance.next();
                }
            } catch {}
        }, 1000);
    }

    if (!data || data.length === 0) {
        try {
            if (
                !document
                    .getElementById("navbar")!
                    .classList.contains("nav-scrolled")
            ) {
                document
                    .getElementById("navbar")!
                    .classList.remove("nav-default");
                anime({
                    targets: "#navbar",
                    background: "#000000",
                    complete: () => {
                        setTimeout(() => {
                            document
                                .getElementById("navbar")!
                                .classList.add("nav-scrolled");
                        }, 100);
                    },
                });
            }
        } catch {}
    }

    return (
        <div>
            {!!data && data.length !== 0 ? (
                <div id="carousel" className="carousel bg-img">
                    {data.map((_val, i) => {
                        return (
                            <a className="carousel-item">
                                <img src={data[i].carouselimg_url} />
                            </a>
                        );
                    })}
                </div>
            ) : (
                <></>
            )}
        </div>
    );
};

export default Carousel;
